<template>
    <home-container >
        <div v-if="step === 1">
            <div class='text-center'>
                <h2 class=''>Personal Information</h2>
                <small class="text-muted">Sign up in 4 easy steps</small>
            </div>
            <div class="d-flex align-items-center mt-3 mb-5">
                <div class="d-flex flex-column">
                    <span class='progress-item lg active'><ion-icon :icon="idCard"></ion-icon></span>
                    <small class='progress-item-label ion-text-nowrap'>In Progress</small>
                </div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="shield"></ion-icon></div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="card"></ion-icon></div>
            </div>
            <ion-text class=''>Just Curious what your parent call you</ion-text>
            <ion-item class='border rounded ion-margin-vertical' lines="none">
                <ion-input required v-model='firstname' placeholder="Enter first name here"></ion-input>
            </ion-item>
            <ion-item class='border rounded ion-margin-vertical' lines="none">
                <ion-input v-model='lastname' placeholder="Enter last name here"></ion-input>
            </ion-item>
            <ion-text class='my-4'>Here is a chance to give your self the name you always wanted</ion-text>
            <ion-item class='border rounded ion-margin-vertical' lines="none">
                <ion-input v-model='username' placeholder="Enter username"></ion-input>
            </ion-item>
            <ion-button @click.prevent="nextStep" strong :disabled="!firstname || !lastname || !username" expand="block" class='my-5'>Advance</ion-button>
        </div>
        <div v-if="step === 2">
            <div class='text-center'>
                <h2 class=''>Account Security</h2>
                <small class="text-muted">Sign up in 4 easy steps</small>
            </div>
            <div class="d-flex align-items-center mt-3 mb-5">
                <div class="d-flex flex-column">
                    <span class='progress-item completed'><ion-icon :icon="idCard"></ion-icon></span>
                    <small class='progress-item-label ion-text-nowrap'>Completed</small>
                </div>
                <div class='progress-item-separator completed'></div>
                <div class="d-flex flex-column">
                    <span class='progress-item lg active'><ion-icon :icon="shield"></ion-icon></span>
                    <small class='progress-item-label ion-text-nowrap'>In Progress</small>
                </div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="card"></ion-icon></div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="diamond"></ion-icon></div>
            </div>
            <div class='text-center'>
                <ion-text class="text-muted">Please create an authentic password to secure <br> your account</ion-text>
            </div>
            <ion-label  position="stacked">Create Password</ion-label>
            <ion-item  class="rounded border ion-margin-bottom" lines="none">
                <ion-input placeholder="Set your password here" :type="showPassword ? 'text':'password'" v-model="password"></ion-input>
                <ion-button @click.prevent="showPassword = !showPassword" color='dark' fill='clear' slot='end'>
                    <ion-icon v-if="!showPassword" slot="icon-only" :icon='eyeOffOutline'></ion-icon>
                    <ion-icon v-else slot="icon-only" :icon='eyeOutline'></ion-icon>
                </ion-button>
            </ion-item>
            <ion-label  position="stacked">Confirm Password</ion-label>
            <ion-item  class="rounded border ion-margin-bottom" lines="none">
                <ion-input placeholder="Confirm password here" :type="showPassword ? 'text':'password'" v-model="confirm_password"></ion-input>
                <ion-button @click.prevent="showPassword = !showPassword" color='dark' fill='clear' slot='end'>
                    <ion-icon v-if="!showPassword" slot="icon-only" :icon='eyeOffOutline'></ion-icon>
                    <ion-icon v-else slot="icon-only" :icon='eyeOutline'></ion-icon>
                </ion-button>
            </ion-item>
            <ion-text>
                <strong>Password must include</strong>
                <ul class='list-unstyled'>
                    <li>Uppercase A-Z</li>
                    <li>Uppercase a-z</li>
                    <li>Numerals 0-9</li>
                    <li>Special character #$%&*_</li>
                </ul>
            </ion-text>
            <ion-button @click.prevent="nextStep" strong :disabled="disable_step_two" class='ion-margin' expand='block'>Advance</ion-button>
        </div>
        <div v-if="step === 3">
            <div class='text-center'>
                <h2 class=''>Account Security</h2>
                <small class="text-muted">Sign up in 4 easy steps</small>
            </div>
            <div class="d-flex align-items-center mt-3 mb-5">
                <div class="d-flex flex-column">
                    <span class='progress-item completed'><ion-icon :icon="idCard"></ion-icon></span>
                    <small class='progress-item-label ion-text-nowrap'>Completed</small>
                </div>
                <div class='progress-item-separator completed'></div>
                <div class="d-flex flex-column">
                    <span class='progress-item lg active'><ion-icon :icon="shield"></ion-icon></span>
                    <small class='progress-item-label ion-text-nowrap'>In Progress</small>
                </div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="card"></ion-icon></div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="diamond"></ion-icon></div>
            </div>
            <ion-label  position="stacked">Email Address</ion-label>
            <ion-item  class="rounded border ion-margin-bottom" lines="none">
                <ion-input type='email' v-model="email" placeholder="Enter your email address here"></ion-input>
            </ion-item>
            <ion-label  position="stacked">Phone Number</ion-label>
            <ion-item  class="rounded border ion-margin-bottom" lines="none">
                <ion-input type="tel" v-model="phone" placeholder="Enter your phone number here"></ion-input>
            </ion-item>
            <ion-button @click.prevent="handleProcess" strong :disabled="!email || !phone" class='ion-margin' expand='block'>Verify</ion-button>
        </div>
        <div v-if="step === 4">
            <div class='text-center'>
                <h2 class=''>Account Security</h2>
                <small class="text-muted">Sign up in 4 easy steps</small>
            </div>
            <div class="d-flex align-items-center ion-margin-vertical">
                <div class="d-flex flex-column">
                    <span class='progress-item completed'><ion-icon :icon="idCard"></ion-icon></span>
                    <small class='progress-item-label ion-text-nowrap'>Completed</small>
                </div>
                <div class='progress-item-separator completed'></div>
                <div class="d-flex flex-column">
                    <span class='progress-item lg active'><ion-icon :icon="shield"></ion-icon></span>
                    <small class='progress-item-label ion-text-nowrap'>In Progress</small>
                </div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="card"></ion-icon></div>
                <div class='progress-item-separator'></div>
                <div class='progress-item'><ion-icon :icon="diamond"></ion-icon></div>
            </div>
            <div class='text-center mb-3'>
                <ion-text class="text-muted">A verification code was sent to your phone<br> and email. Please check them below</ion-text>
            </div>
            <div class="d-flex justify-content-center align-items-center flex-column text-center">
                <ion-label class='ion-margin-bottom'>Email Verification</ion-label>
                <v-otp-input class='mb-2'  @on-complete="handleOnComplete($event,'email')" inputClasses="otp-input" :numInputs="4"></v-otp-input>
                <ion-text><p class="small">Code Expires in,<br /><strong>{{ countdown }}</strong></p></ion-text >
            </div>
            <div class="d-flex justify-content-center align-items-center flex-column text-center">
                <ion-label class='ion-margin-bottom'>Phone Verification</ion-label>
                <v-otp-input class='mb-2'  @on-complete="handleOnComplete($event,'phone')" inputClasses="otp-input" :numInputs="4"></v-otp-input>
                <ion-text><p class="small">Code Expires in,<br /><strong>{{ countdown }}</strong></p></ion-text >
            </div>
            <ion-button :disabled="!disable_step_four" @click.prevent="handleProcess" class="ion-margin-horizontal my-4" expand="block">
                <ion-label>Authorize</ion-label>
            </ion-button>
            <div class="my-4">
                <ion-text><p class="d-flex flex-column align-items-center justify-content-center"><ion-text>Didn't get the code?</ion-text>
                <strong @click="countDownTick" class="text-primary">Request another code</strong></p></ion-text>
            </div>
        </div>
        <div style="height: 100vh" class="d-flex flex-column align-items-center justify-content-center" v-if="step === 5">
            <ion-icon class="icon-container mb-5" style="font-size: 4rem" :icon="checkmarkOutline"></ion-icon>
            <ion-text><h1 class="">Congratulations</h1></ion-text>
            <p class='lead text-center'> Your account has been successfully <br>verified</p>
            <ion-button strong @click.prevent="router.push('/home/dashboard')" class="ion-margin-horizontal my-4 w-100" expand="block">Link your Bank/Card</ion-button>
        </div>

    </home-container>
    
</template>
<script>
import { defineComponent } from "vue";
import {

} from "@ionic/vue";
import {idCard,shield,card,diamond,eyeOffOutline,eyeOutline,checkmarkOutline } from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController } from "@ionic/vue";
import VOtpInput from 'vue3-otp-input';

export default defineComponent({
    components: {VOtpInput },
    data() {
        return {
            step:1,
            firstname:null,
            lastname:null,
            username:null,
            password:null,
            confirm_password:null,
            showPassword:false,
            email:null,
            phone:null,
            countdown: null,
            timer: null,
            disableAuthorization: true,
            otp_email:null,
            otp_phone:null,
        }
    },
    
    computed:{
        disable_step_four(){
            return this.disableAuthorization && (!this.otp_email || !this.otp_phone)
        },
        disable_step_two(){
            return !this.password || !this.confirm_password || (this.password != this.confirm_password)
        }
    },
    setup() {
        const router = useRouter();
        return {
            router,idCard,shield,card,diamond,eyeOffOutline,eyeOutline,checkmarkOutline
        };
    },
    methods:{
        async handleProcess(){
            let loading = await loadingController.create({
                message: "Please wait...",
            });
            loading.present();
            setTimeout(() => {
                this.nextStep();
                loading.dismiss();
            }, 1000);
        },
        countDownTick() {
            clearInterval(this.timer);
            this.disableAuthorization = false;
            let countDownDate = new Date().setMinutes(new Date().getMinutes() + 1);
            this.timer = setInterval(() => {
                var now = new Date().getTime();
                var distance = countDownDate - now;
                var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
                var seconds = Math.floor((distance % (1000 * 60)) / 1000);
                minutes = minutes < 10 ? "0" + minutes : minutes;
                seconds = seconds < 10 ? "0" + seconds : seconds;
                if (distance < 2) {
                clearInterval(this.timer);
                this.disableAuthorization = true;
                return;
                }
                this.countdown = minutes + ":" + seconds;
            }, 1000);
        },
        nextStep() {
            this.step++;
            clearInterval(this.timer);
            if (this.step === 4) {
                this.countDownTick();
            }
        },
        handleOnComplete(e,field){
        if(field == 'email'){
            this.otp_email = e;
        }else{
            this.otp_phone = e;
        }
        if(this.otp_phone && this.otp_email){
            this.disableAuthorization = false;
            this.handleProcess();
        }
    },
    }
});

</script>
<style  scoped>
.progress-item.lg{
    min-width: 2.7rem;
    width:2.7rem;
    height:2.7rem;
    max-height:2.7rem;
}
.progress-item{
    min-width: 2.5rem;
    width:2.5rem;
    height:2.5rem;
    max-height:2.5rem;
    background:#353535;
    border:1px solid #787878;
    border-radius:50%;
    display:flex;
    color:rgb(247, 245, 245);
    align-items: center;
    justify-content: center;
    font-size:1.5rem;
    opacity:0.4;
}
.progress-item-separator{
    width:10rem;
    border:0.2px solid #787878;
}
.progress-item-label{
    font-size:0.5rem;
    color:#787878;
    font-weight:bold
}
.progress-item.active{
    opacity:1;
}
.completed{
    border: 1px solid #735598;
    background:#735598;
    opacity:1;
}

</style>